@import url(https://fonts.googleapis.com/css?family=Red+Rose|Oswald|Lato|Mulish|Montserrat|Odibee+Sans|Open+Sans|Roboto|Recursive|Sriracha&display=swap);
.container {
  width: 100vw;
  display: flex;
  flex-wrap: wrap;
}

.roboto {
  font-family: 'Roboto';
}

.openSans {
  font-family: 'Open Sans';
}

.odibeeSans {
  font-family: 'Odibee Sans';
}

.sriracha {
  font-family: 'Sriracha';
}

.recursive {
  font-family: 'Recursive';
}

.montserrat {
  font-family: 'Montserrat';
}

.mulish {
  font-family: 'Mulish';
}

.lato {
  font-family: 'Lato';
}

.oswald {
  font-family: 'Oswald';
}

.redRose {
  font-family: 'Red rose';
}

.card-container {
  display: flex;
  flex-direction: column;
  min-width: 300px;
  height: 250px;
  margin: 20px;
  border-top: 1px solid darkgrey;
  flex-grow: 1;
}
.info-container {
  margin: 10px 0;
}
.font-name,
.font-author {
  font-family: 'Open Sans';
  margin: 5px;
  font-size: 90%;
  font-weight: 700;
  color: darkgrey;
}

.font-name {
  color: black;
}

.show-text {
  width: 270px;
  flex-grow: 1;
  border: none;
  outline: none;
  font-size: 220%;
  line-height: 40px;
}

.wrapper {
  display: flex;
  justify-content: space-between;
}
.plus {
  height: 20px;
  width: 20px;
  margin-top: 10px;
}
.btn-style {
  display: flex;
  justify-content: space-evenly;
}

.css-container {
  width: 320px;
  font-family: 'Open Sans';
}

.info {
  padding: 5px;
  border-top: 1px solid darkgrey;
}

form {
  background-color: rgba(0, 0, 0, 0.08);
  padding: 5px;
  line-height: 1;
  border-radius: 5px;
  font-size: 15px;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px #999;
}

label {
  margin: 0;
  font-size: 80%;
  color: black;
}

.nav-bar {
  display: flex;
  flex-direction: row;
  height: 45px;
  border: none;
  border-bottom: 1px solid darkgrey;
  border-radius: 0;
  align-items: center;
  width: 100%;
  margin: 20px;
  padding: 2px;
}
.input {
  border: none;
  font-size: 20px;
  height: 100%;
  font-family: 'Open Sans';
  width: 30%;
  outline: none;
}

